/*! Grid based on https://github.com/necolas/griddle by Nicolas Gallagher (@necolas) */
body{font-size:1em}
.wrapper{margin-left:auto;margin-right:auto;padding-left:1.25em;padding-right:1.25em}
img{max-width:100%}
.no-js .responsive-image{display:none}
.grid{display:block;padding:0;margin:0 -.625em;text-align:left;letter-spacing:-.31em;text-rendering:optimizespeed}
.grid,.opera:-o-prefocus{word-spacing:-.43em}
.grid>.grid{overflow:hidden;margin-right:0;margin-left:0}
.grid__cell{width:100%;display:-moz-inline-stack;display:inline-block;zoom:1;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box;margin:0;padding:0 .625em;vertical-align:top;text-align:left;letter-spacing:normal;word-spacing:normal;text-rendering:auto}
.grid--center{text-align:center}
.grid__cell--center{display:block;margin:0 auto}
a{text-decoration:none}
a [href]:hover{background:#ffa}
a[href^=http]{text-decoration:underline}
blockquote{border-left:5px solid #f44336;margin-bottom:1.5em;margin-left:1em;margin-top:1.5em;padding-left:1em}
aside.pull{margin-left:-20%;margin-top:.2em;margin-bottom:.5em}
.opening{color:#f44336;font-size:1.5em;line-height:1.2}
.content-left,.margin-notes{width:230px;margin-left:-230px;margin-right:20px;text-align:right;font-size:.75em}
.content-right,.margin-notes-right{width:230px;margin-left:760px;margin-right:-20px;text-align:left;font-size:.75em;position:absolute}
.content-right{width:230px;margin-left:760px;margin-right:-20px;text-align:left;font-size:.75em;position:absolute}
.content-left ul,.content-right ul{list-style:none}
aside.lowlight{opacity:.3}
aside.lowlight:hover{opacity:1}
aside h3{text-align:center;padding-left:20px}
html{text-rendering:optimizeLegibility;-webkit-font-variant-ligatures:common-ligatures;font-variant-ligatures:common-ligatures;text-rendering:optimizeLegibility;-moz-font-feature-settings:"kern=1";-webkit-font-feature-settings:"kern";-moz-font-feature-settings:"kern";font-feature-settings:"kern";font-kerning:normal;color:#554c4d;font-family:Stone-Humanist;line-height:1.3}
h1,h2,h3,h4,h5,h6{color:555;font-family:'Roboto Slab',sans-serif;hyphens:none}
h1{font-size:2em;letter-spacing:-1px;line-height:1.25;margin-top:.9375em;margin-bottom:.3125em}
h3{font-size:1.375em;line-height:1.13636364;margin-top:1.36363637em;margin-bottom:.45454546em}
h4{font-size:1.125em;line-height:1.11111111;margin-top:1.66666667em;margin-bottom:.55555556em}
strong{font-family:Stone-Humanist-Semi}
em{font-family:Stone-Humanist-Ita}
em strong,strong em{font-family:Stone-Humanist-SemiItalic}
object,video{border-width:thin;border-style:solid;clear:both;display:block;margin:1em auto;max-width:100%;width:100%}
div.video{margin:1em auto;width:100%;max-width:100%}
div.video iframe{clear:both;max-width:100%}
div.video p{font-style:italic;font-weight:700;margin-top:-.125em}
div>h2>p.cap::first-line{color:red;font-size:130%}
p p{text-indent:2em}
p .small{font-size:90%}
section{counter-increment:section_count;counter-reset:figure_count}
figure{counter-increment:figure_count;margin:2em auto;display:block}
figure figcaption{color:#999;font-family:Stone-Humanist-SemiItalic,sans-serif;margin-top:-.25em}
figure figcaption::before{content:"Figure " counter(section_count) "-" counter(figure_count) ": "}
figure img{display:block;width:100%;max-width:600px;max-width:100%;height:auto}
figure .sidebyside{counter-increment:figure_count;margin:2em auto;display:block;width:100%;max-width:600px;max-width:100%;height:auto}
figure .sidebyside figcaption{font-family:Stone-Humanist-SemiItalic,sans-serif;margin-top:-.25em}
figure .sidebyside figcaption::before{content:"Figure " counter(section_count) "-" counter(figure_count) ": "}
figure .sidebyside img{display:block;width:100%;max-width:600px;max-width:100%;height:auto}
figure .sidebyside img :last-child{clear:both}
.full-bleed{background-size:cover;height:50vw}
.full-bleed p{margin-top:1.5em}
.full-bleed p :before{content:" ";display:block;clear:both}
.full-bleed-image{display:none}
div .code-example{font-family:sourcecodepro-medium-webfont,monospace;background-color:#666;color:#fff;padding:20px;white-space:pre-line;display:block}
pre{white-space:pre-line}
pre code{font-family:sourcecodepro-medium-webfont,monospace;font-size:80%}
style{font-family:sourcecodepro-medium-webfont,monospace;background-color:#333;color:#fff;padding:20px;white-space:pre-line;clear:both;margin-bottom:.5em}
div .css-example{width:100%;counter-increment:css_count}
div .css-example+p:before{content:"CSS Example " counter(section_count) "-" counter(css_count) ": "}
iframe .cp_embed_iframe{counter-increment:css_count}
iframe .cp_embed_iframe+p:before{content:"CSS Example " counter(section_count) "-" counter(css_count) ": "}
